import { getRandomNumber } from './utils';

function swap(array, i, j) {
  if (i === j) {
    return;
  }

  var temp = array[i];
  array[i] = array[j];
  array[j] = temp;
}

export function fisherYatesInArrayShuffle(array, getRandom) {
  for (var i = array.length - 1; i >= 1; i -= 1) {
    var j = getRandomNumber(0, i, getRandom);
    swap(array, i, j);
  }
}

function partition(start, end, array, keys) {
  var pivot = keys[end];
  var index = start;

  for (var j = start; j < end; j += 1) {
    if (keys[j] > pivot) {
      swap(array, index, j);
      swap(keys, index, j);
      index += 1;
    }
  }

  swap(array, index, end);
  swap(keys, index, end);
  return index;
}

function sort(start, end, array, keys) {
  if (start >= end) {
    return;
  }

  var index = partition(start, end, array, keys);
  sort(start, index - 1, array, keys);
  sort(index + 1, end, array, keys);
}

export function weightedShuffle(array, getWeight) {
  var keys = array.map(function (element, i) {
    var weight = getWeight(element, i);
    var key = Math.pow(Math.random(), 1 / weight);
    return key;
  });
  sort(0, array.length - 1, array, keys);
}